header {
  height: 400px;
  position: relative;
  overflow: hidden;
}

header .parallax {
  min-width: 100%;
  height: 400px;
}

header::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 12;
  background-image: repeating-linear-gradient(180deg, #0e5fd4, #0e5fd455 25%, #0e60d455 100%);
}

header .header-container {
  position: absolute;
  bottom: 50px;
  z-index: 22;
  width: 100%;
  left: 0;
  right: 0;
}

header .header-container .page-title {
  color: white;
}

header .header-container .page-title h2 {
  font-family: "Montserrat-Medium", Arial, Helvetica, sans-serif;
  font-size: 32px;
  text-transform: uppercase;
}

header .header-container .page-title p {
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
}

.about-us {
  background-color: white;
  box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.2);
  text-align: left;
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
}

@media only screen and (min-width: 768px) {
  .about-us {
    padding: 25px 30px;
  }
}

@media only screen and (max-width: 767px) {
  .about-us .col-lg-7 {
    padding: 15px 25px;
  }
}

.about-us h2 {
  font-size: 30px;
  color: #002c42;
  line-height: 1.2;
}

.about-us p {
  font-size: 14px;
  color: #606060;
  line-height: 1.375;
}

.about-us__content {
  overflow-y: auto;
  max-height: 280px;
  padding-right: 5px;
}

.about-us .phone {
  margin-top: 20px;
  display: flex;
  width: 100%;
}

.about-us .phone-icon {
  width: 20%;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("../img/phone-icon_bg.png");
}

@media only screen and (max-width: 767px) {
  .about-us .phone-icon {
    width: 25%;
  }
  .about-us .phone-icon img {
    max-width: 40px;
  }
}

.about-us .phone-number {
  width: 80%;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-image: url("../img/phone-number_bg.png");
}

@media only screen and (max-width: 767px) {
  .about-us .phone-number {
    width: 75%;
  }
}

.about-us .phone-number p {
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
  text-align: center;
  color: white;
  font-size: 18px;
  margin: 0;
  transform: translateY(5px);
}

@media only screen and (max-width: 767px) {
  .about-us .phone-number p {
    font-size: 16px;
  }
}

.about-us .phone-number a {
  color: white;
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
  text-align: center;
  font-size: 40px;
}

@media only screen and (max-width: 767px) {
  .about-us .phone-number a {
    font-size: 28px;
  }
}

