.btn-more {
  height: 46px;
  font-size: 14px;
  font-family: "Montserrat-Medium", Arial, Helvetica, sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 1.5;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-more__blue {
  border-radius: 23px;
  background-color: #0e5fd4;
  box-shadow: 0px 2px 5px 0px rgba(15, 46, 81, 0.41);
  color: white;
  padding: 12px 25px;
  min-width: 200px;
}

.btn-more__blue:hover {
  color: #333;
  background-color: #dee6ee;
  color: #292f3b;
}

.btn-more__blue-square {
  border-radius: 0;
  background-color: #1284d3;
  background-image: url("../img/blue-square.png");
  min-width: 170px;
  font-size: 16px;
  color: white;
  text-transform: uppercase;
  font-weight: normal;
}

.btn-more__blue-square:hover {
  color: white;
  background-image: none;
}

.btn-more__blue-square-2 {
  min-width: unset;
  max-width: 200px;
}

.btn-more__white {
  border-radius: 23px;
  background-color: #dee6ee;
  color: #292f3b;
  padding: 6px 25px;
  min-width: 150px;
}

.btn-more__white:hover {
  color: #fff;
  background-color: #0e5fd4;
}

.btn-more__news {
  border-radius: 0;
  background-color: #1284d3;
  width: 170px;
  font-size: 14px;
  color: white;
  text-transform: uppercase;
  font-weight: normal;
  padding: 5px 10px;
}

.btn-more__news:hover {
  color: white;
  background-color: #0e5fd4;
  opacity: .9;
}

