.navbar {
  position: absolute;
  top: 45px;
  width: 100%;
  z-index: 99;

  @include mq("tablet", max) {
    padding-left: 15px;
    padding-right: 15px;
  }

  

  &-toggler {
    margin-top: 2rem;
    @include mq("tablet-wide", max) {
      transform: translateX(10px);
    }
  }

  &-collapse {
    @include mq("tablet-wide", max) {
      background-color: #fff;
      box-shadow: rgba(15, 46, 81, 0.2) 0px 16px 35px 0px;
      // border-radius: 15px;
    }
  }

  .nav {
    &-item {
      font-size: 14px;
      font-family: "Montserrat-Medium", Arial, Helvetica, sans-serif;
      text-transform: uppercase;
      color: rgb(255, 255, 255);

      margin: 5px 10px;

      border: 2px solid transparent;
      transition: all 0.2s;

      @include mq("tablet-wide", min) {
        &:last-of-type,
        &:first-of-type {
          margin-right: 0;
        }

        &:hover,
        &.active {
          box-shadow: 0px 0px 0.98px 0.02px rgba(0, 3, 6, 0.3);
          border-color: rgba(255, 255, 255, 0.631);
        }
      }

      @include mq("tablet-wide", max) {
        display: flex;
        justify-content: center;

        &.active,
        &:hover {
          .nav-link {
            border-bottom-color: #333;
            margin: 0 auto;
          }
        }
      }
    }

    &-link {
      border: 1px solid transparent;

      @include mq("tablet-wide", max) {
        display: inline-block;
        margin: 0 auto;
        text-align: center;
        color: #333;
      }
    }
  }
}
