.subscribe-for-news {
  display: block;
  @include mq("tablet", max) {
    margin-left: 15px;
    margin-right: 15px;
  }
  input,
  button {
    height: 43px;
    border: 1px solid rgba(0, 0, 0, 0.3);
    padding: 8px 12px;
    width: 100%;
    margin-bottom: 15px;
    display: block;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    font-size: 14px;
    font-weight: normal;
  }

  input {
    &:focus {
      border-color: $my-blue;
    }
  }

  button {
    color: white;
    background-color: $my-blue;
    text-transform: uppercase;
    text-align: center;
    border-color: $my-blue;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;

    &:hover {
      background-color: rgba($my-blue, 0.9);
      color: white;
    }
  }
}

.form-control {
  border-radius: 3px;
  font-size: 13px;
  height: auto;
  line-height: 1.33333;
  padding: 15px 15px 15px 20px;
  background: #f6f7f8;
  color: #323232;
  border: 0px solid transparent;
  box-shadow: none;
}

.form-control:focus {
  background: #ffffff;
  border: 0px;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.5s ease-out 0s;
  -moz-transition: all 0.5s ease-out 0s;
  -ms-transition: all 0.5s ease-out 0s;
  -o-transition: all 0.5s ease-out 0s;
  transition: all 0.5s ease-out 0s;
  border-color: transparent;
}

.gray-form {
  .btn-submit {
    padding: 8px 12px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: white;
    background-color: $my-blue;
    text-transform: uppercase;
    text-align: center;
    border: none;
    font-size: 14px;

    border-color: $my-blue;
    &:hover {
      background-color: rgba($my-blue, 0.9);
      color: white;
    }
  }
  @include mq("tablet", max) {
    margin-bottom: 20px;
    padding: 20px 0;
    border-bottom: 1px dashed #d0d0d0;
    border-top: 1px dashed #d0d0d0;
  }
}
