.swiper-container {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-header {
  &__item {
    @extend %bgcover;
    height: 800px;
    width: 100%;

    @include mq("tablet", max) {
      height: 100vh;
    }

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    position: relative;
    z-index: 22;

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 12;
      background-image: repeating-linear-gradient(
        180deg,
        #0e5fd4,
        #0e5fd444 25%,
        #0e5fd444 25%,
        #0e60d400 100%
      );
    }

    * {
      position: relative;
      z-index: 22;
    }

    p {
      max-width: 992px;
      text-shadow: -0.052px -3px 0.98px rgba(0, 3, 6, 0.3);
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      color: rgb(254, 254, 254);
      text-transform: uppercase;
      line-height: 1.182;
      text-align: center;
      font-size: 42px;

      @include mq("tablet", max) {
        padding: 0 15px;
        font-size: 24px;
      }
    }
  }

  .swiper-pagination-header {
    bottom: 150px;

    @include mq("tablet", max) {
      bottom: 50px;
    }
  }

  .swiper-button-next {
    right: 8%;
  }
  .swiper-button-prev {
    left: 8%;
  }

  .swiper-button-next,
  .swiper-button-prev {
    width: 50px;
    height: 50px;
    &::after {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      color: white;
      width: 50px;
      height: 50px;
      background-color: rgba(255, 255, 255, 0.15);
      transition: all 0.2s;
    }
    &:hover {
      &::after {
        background-color: rgba(0, 0, 0, 0.4);
      }
    }

    @include mq("tablet", max) {
      display: none;
    }
  }

  .swiper-pagination-bullet {
    background-color: transparent;
    border: 1px solid #fff;
    opacity: 1;

    &-active {
      background-color: #fff;
    }
  }
}

.swiper-service {
  @include mq("tablet", min) {
    margin-top: -150px;
  }
  &__item {
    background-color: rgb(255, 255, 255);

    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    padding: 25px;
    height: 300px;

    position: relative;
    z-index: 22;
    transition: all 0.2s;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      // border-left: 1px solid rgba(15, 46, 81, 0.2);
      // border-bottom: 1px solid rgba(15, 46, 81, 0.2);
      box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.1);
      transition: all 0.2s;
    }

    &:hover {
      z-index: 33;
      &::after {
        box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.2);
      }
    }

    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    font-weight: normal;
    text-align: center;

    div {
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    h2 {
      font-size: 18px;
      color: rgb(41, 47, 59);
      text-transform: uppercase;
      line-height: 1.2;
    }

    p {
      font-size: 14px;
      color: rgb(96, 96, 96);
      line-height: 1.571;
    }
  }

  & ~ .swiper-button-next {
    @include mq("tablet-wide", min) {
      right: -35px;
    }
    right: 0px;
  }
  & ~ .swiper-button-prev {
    @include mq("tablet-wide", min) {
      left: -35px;
    }
    left: 0px;
  }

  & ~ .swiper-button-next,
  & ~ .swiper-button-prev {
    opacity: 1;
    width: 50px;
    height: 50px;
    box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.2);
    &::after {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      color: $my-blue;
      width: 50px;
      height: 50px;
      background-color: rgba(255, 255, 255, 1);
      transition: all 0.2s;
    }

    // &:hover {
    //   &::after {
    //   }
    // }

    // @include mq("tablet", max) {
    //   // display: none;
    // }
  }
}

.slider-project {
  &__item {
    background-image: url("../img/slider-project_bg.png");
    @extend %bgcover;
    @include mq("tablet", min) {
      padding: 15px 25px;
    }
    padding: 15px;

    &--content {
      overflow-y: auto;
      max-height: 300px;
    }

    h2 {
      font-size: 20px;
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      font-weight: normal;
      color: rgb(255, 255, 255);
      text-transform: uppercase;
      line-height: 1.5;
      text-align: center;

      @include mq("tablet", max) {
        font-size: 18px;
      }
    }

    address {
      font-size: 12px;
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      font-weight: normal;
      color: rgb(255, 255, 255);
      line-height: 1.714;
    }

    p {
      font-size: 16px;
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      font-weight: normal;
      color: rgb(255, 255, 255);
      line-height: 1.5;
    }

    ul {
      padding-left: 18px;
      li {
        font-size: 14px;
        font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
        font-weight: normal;
        color: rgb(255, 255, 255);
        line-height: 1.714;
      }
    }

    .more-info {
      display: flex;
      align-items: center;
      width: 100%;
      height: 50px;
      @extend %bgcover;
      @include mq("tablet", min) {
        background-image: url("../img/more-info_bg.png");
      }
      @include mq("tablet", max) {
        background-color: #fff;
      }
      flex-wrap: wrap;

      &__email {
        width: 40%;
        font-size: 16px;
        font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
        color: rgb(20, 52, 101);
        text-align: center;
        @include mq("tablet", max) {
          width: 100%;
        }
      }

      &__phone {
        width: 35%;
        font-size: 16px;
        font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
        color: rgb(1, 69, 150);
        text-transform: uppercase;
        text-align: center;
        @include mq("tablet", max) {
          width: 50%;
        }
      }

      &__icons {
        text-align: center;
        width: 25%;
        color: $my-blue;
        @include mq("tablet", max) {
          width: 50%;
        }

        a {
          font-size: 14px;
          color: $my-blue;
          margin-right: 10px;
        }
      }
    }

    &--image {
      @extend %bgcover;
      height: 500px;
      @include mq("desktop", max) {
        height: 600px;
      }
    }
  }

  .slick {
    &-next {
      opacity: 1;
      @include mq("tablet", min) {
        right: -20px;
        top: 61%;
      }
      right: -15px;
      z-index: 100;
      &::before {
        content: "\f054";
        font-family: "Font Awesome 5 Pro";
        font-weight: 500;
        -webkit-font-smoothing: antialiased;
        display: inline-block;
        font-style: normal;
        font-variant: normal;
        text-rendering: auto;
        line-height: 1;
        opacity: 1;
      }
    }

    &-prev {
      opacity: 1;
      @include mq("tablet", min) {
        left: unset;
        right: -20px;
      }
      z-index: 100;
      left: -15px;
      &::before {
        content: "\f053";
        font-family: "Font Awesome 5 Pro";
        font-weight: 500;
        -webkit-font-smoothing: antialiased;
        display: inline-block;
        font-style: normal;
        font-variant: normal;
        text-rendering: auto;
        opacity: 1;

        line-height: 1;
      }
    }

    &-prev,
    &-next {
      width: 48px;
      height: 48px;
      &::before {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 14px;
        color: rgb(136, 136, 136);

        width: 48px;
        height: 48px;
        transition: all 0.2s;
        border-width: 2px;
        border-color: rgb(20, 33, 57);
        border-style: solid;
        background-color: rgb(24, 81, 148);
      }
      &:hover {
        &::before {
          background-color: rgb(15, 25, 43);
        }
      }
    }
  }
}

.swiper-partners {
  .swiper-slide {
    height: calc((100% - 30px) / 2);

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
}

.swiper-about-us {
  img {
    width: 100%;
    height: 350px;
    object-fit: cover;

    @include mq("tablet", max) {
      height: 300px;
    }
  }

  .swiper-pagination-bullet {
    background-color: transparent;
    border: 1px solid #fff;
    opacity: 1;
  }
  .swiper-pagination-bullet-active {
    background-color: white;
  }

  .swiper-pagination-bullets,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    bottom: 25px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 3, 6, 0.3);

    @include mq("tablet-wide", max) {
      bottom: 0;
    }
  }
}
