.top-block {
  background-image: url("../img/top-block_bg.png");
  @extend %bgcover;
  position: absolute;
  width: 100%;
  z-index: 98;
  height: 45px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.102);

  color: rgb(255, 255, 255);
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 1.714;
  z-index: 100;
  display: flex;

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mq("tablet", max) {
      flex-direction: column;
    }
  }

  @include mq("tablet-wide", max) {
    font-size: 10px;
  }
  @include mq("tablet", max) {
    font-size: 10px;
    padding: 5px 0;
    height: 50px;
  }

  @include mq("tablet", max) {
    height: 50px;
  }

  &__left,
  &__right {
    &--item {
      a {
        i {
          margin-right: 5px;
        }
      }
      display: inline-block;
      position: relative;
      &:not(:last-of-type) {
        margin-right: 25px;

        &::after {
          content: "|";
          position: absolute;
          right: -15px;
          top: 45%;
          transform: translateY(-50%);
        }
      }
    }
  }
}

.social-icons {
  font-size: 12px;

  a {
    margin: 0 5px;

    &:first-of-type {
      margin-left: 0;
    }
    &:last-of-type {
      margin-right: 0;
    }
  }
}

.navbar-brand {
  @include mq("tablet", max) {
    margin-right: 10px;
  }
  img {
    max-width: 100px;

    @include mq("desktop", max) {
      max-width: 75px;
    }
  }

  &__name {
    color: white;
    font-family: "RopaSansSCPro-Regular", Arial, Helvetica, sans-serif;
    font-size: 14px;

    div {
      font-size: 30px;
      text-align: center;

      @include mq("desktop", max) {
        font-size: 25px;
      }
      @include mq("tablet", max) {
        font-size: 22px;
      }
    }
  }
}

.subscribe {
  @include mq("tablet", max) {
    margin-top: 10px;
  }
  label {
    font-size: 16px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: rgb(255, 255, 255);
    text-align: center;
  }

  .input-group {
    min-width: 400px;
    @include mq("tablet", max) {
      min-width: 300px;
    }

    .form-control {
      height: 35px;
      border: none;
      border-radius: 0;
      font-size: 12px;
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      color: rgb(90, 89, 106);
    }

    &-append {
      display: flex;
      justify-content: center;
      align-items: center;

      button {
        font-size: 16px;
        font-family: "Montserrat-Light", Arial, Helvetica, sans-serif;
        text-transform: uppercase;
        width: 50px;
        height: 35px;
        border-top-right-radius: 20px;
        border-bottom-right-radius: 20px;
        padding: 5px;
      }
    }
  }
}

.breadcrumb {
  background-color: rgba(#0e5fd4, 0.8);
  padding: 10px;
  border: 0;
  border-radius: 0;
  i {
    font-size: 14px;
  }
  &-item {
    font-size: 14px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    line-height: 1.2;

    &.active {
      color: rgba($color: #fff, $alpha: 0.6);
    }
    a {
      color: white;
    }
    & + &::before {
      display: inline-block;
      padding-right: 0.5rem;
      color: #fff;
      opacity: 1;
      transform: translateY(1px);
      content: "\f101";
      font-family: "Font Awesome 5 Pro";
      font-weight: 300;
    }
  }
}

.pagination {
  flex-wrap: wrap;
  .page-item:first-child .page-link,
  .page-item:last-child .page-link {
    border-radius: 0;
  }

  .page {
    &-item {
      margin-right: 10px;
      margin-top: 10px;

      &.active,
      &:hover,
      &:focus {
        a {
          box-shadow: unset !important;
          outline: none !important;
        }
      }
    }

    &-link {
      font-size: 18px;
      font-family: "Montserrat-Medium", Arial, Helvetica, sans-serif;
      color: rgb(17, 17, 17);
      line-height: 1;
      background-color: rgb(255, 255, 255);
      box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.2);
      min-width: 45px;
      justify-content: center;
      align-items: center;
      display: flex;
      padding-top: 10px;
      padding-bottom: 10px;

      &:hover {
        color: rgb(17, 17, 17);
      }

      &:focus {
        box-shadow: unset;
      }
    }
  }
}

.share {
  display: flex;
  align-items: center;
  @include mq("tablet", max) {
    margin-bottom: 10px;
  }
  p {
    margin-right: 10px;
    font-size: 14px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    font-weight: 400;
    color: rgb(17, 17, 17);
    margin-bottom: 0;
  }

  &-links {
    a {
      color: $my-blue;
      margin-right: 5px;
      font-size: 14px;
      display: inline-block;
      position: relative;
      &:hover {
        // color: white;
      }
    }
  }
}

.tags {
  display: flex;
  align-items: center;
  @include mq("tablet", max) {
    margin-bottom: 10px;
  }
  p {
    margin-right: 10px;
    font-size: 14px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: rgb(17, 17, 17);
    margin-bottom: 0;
  }
  &-list {
    list-style-type: none;
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0;
  }
  &-item {
    margin-right: 10px;
    position: relative;
    &:not(:last-of-type)::after {
      position: absolute;
      content: ",";
      top: 0;
      right: 0;
    }
    a {
      color: $my-blue;
      margin-right: 5px;
      display: inline-block;
      position: relative;
      font-size: 12px;
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      font-weight: 400;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.port-navigation {
  padding: 20px 0;
  border-bottom: 1px dashed #d0d0d0;
  border-top: 1px dashed #d0d0d0;

  @include mq("tablet", max) {
    padding: 20px 15px;
  }
}
.port-navigation a {
  color: #626262;
}
.port-arrow {
  height: 77px;
  width: 40px;
  text-align: center;
  background: #f7f7f7;
  display: inline-block;
}
.port-arrow i {
  text-align: center;
  font-size: 25px;
  line-height: 75px;
}
.port-photo {
  width: 124px;
  height: 77px;
  display: inline-block;

  @include mq("tablet", max) {
    width: 100px;
  }
}
.port-photo img {
  width: 100%;
  height: 76px;
  object-fit: cover;
}

.blog-comments,
.gray-form,
.theme-color {
  @include mq("tablet", max) {
    margin-left: 15px;
    margin-right: 15px;

    [class*="col-"] {
      padding: 0;
    }
  }
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
}

.comments-1 .comments-photo {
  width: 106px;
  height: auto;
  float: left;
  border: 5px solid #f7f7f7;
  margin-right: 20px;
}
.comments-1 .comments-photo img {
  width: 100%;
}
.comments-1 .comments-info {
  display: table;
  background: #f7f7f7;
  padding: 20px;
  margin-bottom: 20px;

  p {
    font-size: 14px;
  }
}
.comments-1 .comments-info h4 {
  display: inline-block;
}
.comments-1 .comments-info span {
  font-size: 13px;
  color: #626262;
}
.comments-1 .comments-info a {
  color: $my-blue;
}
.comments-1 .comments-info a:hover {
  color: #363636;
}
.comments-1.comments-2 {
  padding-left: 127px;
}
.comments-1.comments-2 .comments-info {
  background: #fff;
  margin-bottom: 0px;
  padding: 20px 20px 10px;
}

@media (max-width: 600px) {
  .port-post-social.float-right {
    float: none !important;
  }
  .port-post-photo {
    display: block;
    float: none;
  }
  .port-post-info {
    padding-left: 0;
    margin-top: 20px;
    display: block;
  }
  .comments-1.comments-2 {
    padding-left: 40px;
  }
  .comments-1 .comments-photo {
    float: none;
  }
  .comments-1.comments-2 .comments-info {
    padding: 20px 0px 10px;
  }
  .comments-1 .comments-photo {
    margin-bottom: 20px;
  }
  .comments-1.comments-2 .comments-photo {
    margin-bottom: 0px;
  }
}

/*port-post-info*/
.port-post-info {
  padding-left: 190px;
}
.port-post-info h3 {
  display: inline-block;
  font-size: 22px;
}
.port-post-info span {
  color: #626262;
}
.port-post-social strong {
  font-size: 14px;
}
.port-post-social a {
  color: #626262;
  margin-left: 8px;
  font-size: 14px;
}
.port-post-social a:hover {
  color: $my-blue;
}
.port-post-info p {
  margin-top: 10px;
}
