  .port-post-social.float-right {
    float: none !important;
  }
}
@media (max-width: 600px) {
  .port-post-social.float-right {
    float: none !important;
  }
}

.theme-color {
  color: #333;
}

.myaccordion {
  .card {
    background-color: transparent;
    border: unset;
    padding-left: 0;
    padding-right: 0;
    &-header {
      padding-left: 0;
      padding-right: 0;

      background-color: transparent;
      border: unset;
    }

    &-body {
      text-align: left;
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      font-size: 14px;
      line-height: 21px;
      color: #222;
      opacity: 1;
      padding: 10px 15px 10px;
    }

    .collapse {
      &.show {
      }
    }
  }
  button {
    position: relative;
    padding-left: 0;
    width: 100%;
    text-align: left;
    font-family: "Montserrat-Medium", Arial, Helvetica, sans-serif;
    font-size: 18px;
    line-height: 24px;
    color: #353535;
    background-color: transparent;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
    background: #ffffff;
    padding: 20px;
    border: unset;
    border-radius: unset;

    border-top: 1px solid transparent;

    &:not(.collapsed) {
    }

    margin-bottom: 10px;
  }
  .fa-stack {
    position: absolute;
    top: 20px;
    right: 10px;
    font-size: 12px;
    color: $my-blue;

    @include mq("tablet", max) {
      right: 5px;
    }
  }
}

