.footer {
  &__top {
    background-image: url("../img/footer-top_bg.png");
    @extend %bgcover;
    min-height: 300px;
    padding: 15px 0;
  }

  &__info {
    color: white;

    p {
      font-size: 12px;
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      color: rgb(194, 194, 194);
    }

    ul {
      padding: 0;
      li {
        list-style: none;
        font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
        color: rgb(194, 194, 194);
        line-height: 1.7;
        font-size: 14px;
        margin-bottom: 5px;
        a {
        }
      }
    }
  }

  &__title {
    font-size: 14px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: rgb(255, 255, 255);
    line-height: 1.714;
    font-weight: normal;
    text-transform: uppercase;
    margin-bottom: 15px;
  }

  &__socials {
    margin-top: 20px;
    a {
      height: 35px;
      width: 35px;
      background-color: #2e4b7e;

      margin-right: 10px;
      display: inline-block;
      position: relative;
      i {
        position: absolute;
        @extend %c-c;
      }

      &:hover {
        background-color: $my-blue;
        color: white;
        transform: translateY(-5px);
      }
    }
  }

  &__bottom {
    color: white;
    background-color: #001942;
    padding: 10px 0;
    font-size: 12px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: rgb(211, 211, 211);
    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;

      @include mq('tablet',max) {
        flex-direction: column;
      }
    }

    &--links {
      @include mq('tablet',max) {
        margin-top: 5px;
      }
      &>div {
        font-size: 12px;
        font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
        color: rgb(211, 211, 211);
        position: relative;
        display: inline-block;
        &:not(:last-of-type) {
          margin-right: 15px;

          &::after {
            content: "|";
            position: absolute;
            right: -10px;
          }
        }
      }
    }
  }
}
