/*youtube-video*/
.popup-video {
  position: relative;
}
.popup-video .play-video {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
}
.popup-video .play-video {
  width: 50px;
  height: 50px;
}
.popup-video .play-video a {
  width: 50px;
  height: 50px;
}
.popup-video .play-video a i {
  line-height: 50px;
}
.popup-video .play-video:hover {
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  background-color: #ffffff;
}
.popup-video .play-video:hover a {
  color: $my-blue;
}
.play-video {
  cursor: pointer;
  position: relative;
  width: 100px;
  height: 100px;
  margin: auto;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.play-video {
  background-color: $my-blue;
}
.play-video a {
  color: #fff;
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 50%;
  padding-left: 5px;
  transform: translateX(-50%);
}
.play-video a i {
  line-height: 100px;
}
.play-video:hover a {
  color: $my-blue;
}
.play-video:after {
  content: "";
  top: 0;
  left: 0;
  padding: 0;
  z-index: -1;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  -webkit-box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.4);
  -moz-box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.4);
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.4);
}

.play-video:hover {
  background: #ffffff;
  color: $my-blue;
  border-color: transparent !important;
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -o-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
  -webkit-box-shadow: 2px 6px 18px transparent !important;
  -moz-box-shadow: 2px 6px 18px transparent !important;
  box-shadow: 2px 6px 18px transparent !important;
}
.play-video:hover:after {
  -webkit-animation: sonarEffect 1.3s ease-out 75ms;
  -moz-animation: sonarEffect 1.3s ease-out 75ms;
  animation: sonarEffect 1.3s ease-out 75ms;
}
@-webkit-keyframes sonarEffect {
  0% {
    opacity: 0.3;
  }
  40% {
    opacity: 0.5;
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #ffffff,
      0 0 0 10px rgba(255, 255, 255, 0.5);
  }
  100% {
    -webkit-transform: scale(1.5);
    opacity: 0;
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #ffffff,
      0 0 0 10px rgba(255, 255, 255, 0.5);
  }
}
@-moz-keyframes sonarEffect {
  0% {
    opacity: 0.3;
  }
  40% {
    opacity: 0.5;
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #ffffff,
      0 0 0 10px rgba(255, 255, 255, 0.5);
  }
  100% {
    -moz-transform: scale(1.5);
    opacity: 0;
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #ffffff,
      0 0 0 10px rgba(255, 255, 255, 0.5);
  }
}
@keyframes sonarEffect {
  0% {
    opacity: 0.3;
  }
  40% {
    opacity: 0.5;
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #ffffff,
      0 0 0 10px rgba(255, 255, 255, 0.5);
  }
  100% {
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px #ffffff,
      0 0 0 10px rgba(255, 255, 255, 0.5);
    transform: scale(1.5);
    opacity: 0;
  }
}
