.video_modal {
  .modal {
    &-content,
    &-body {
      background-color: transparent;
      position: relative;
      border: 0;
      border-radius: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0;
      padding: 0;
      .close {
        position: absolute;
        top: -25px;
        right: -25px;
        color: white;
        z-index: 999;
        opacity: 1;
      }

      iframe, video {
        @include mq('tablet', max) {
          max-width: 100%;
          width: unset !important;
          // height: unset;
          max-height: 300px;
        }
      }
    }
  }
}
