.search-cart {
  display: inline-block;
}
.search-cart i.icon {
  color: #ffffff;
  line-height: 20px;
  font-size: 14px;
  transition: 0.5s ease-in-out;
}
.search-cart i.icon:hover {
  color: #84ba3f;
}
.search-cart .search {
  display: inline-block;
  padding-right: 15px;
  padding-left: 10px;
}
.search-cart .shpping-cart {
  display: inline-block;
}

.search-box {
  background: #fff;
  display: block;
  margin: 0;
  opacity: 0 !important;
  padding: 10px 15px;
  position: absolute;
  right: 0;
  top: 150%;
  transition: visibility 300ms ease 0s, opacity 300ms ease 0s;
  visibility: hidden;
  width: 327px;
  z-index: 999999;
  border-radius: 3px;
  transform: rotateX(90deg);
  transform-origin: 0 0;
  transition: all 400ms ease;

  @include mq("tablet", max) {
    right: unset;
    left: -50%;
    min-width: 100%;
  }
}

.search .search-box input {
  background: transparent;
  border: 1px solid #eceff8;
  padding-left: 15px;
  border-radius: 3px;
  height: 40px;
  width: 100%;
}

.search .search-box button {
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 15px;
  height: 38px;
  position: absolute;
  right: 16px;
  top: 50%;
  width: 50px;
  transform: translateY(-50%);
  transition: 0.5s ease-in-out;
  background-color: #fff;
}
@media (min-width: 992px) {
  .search .search-btn {
    vertical-align: middle;
    position: relative;
  }
}

.search .search-btn {
  position: relative;
  color: #ffffff;
  transition: color 300ms ease 0s;
  transition: 0.5s ease-in-out;
}

@media (min-width: 992px) {
  .search-cart .search {
    vertical-align: middle;
  }
}

.search-cart .search {
  display: inline-block;
  padding-right: 15px;
  padding-left: 10px;
}

@media (min-width: 992px) {
  .search-cart {
    height: 100%;
  }
}

.search.search-open .search-box {
  opacity: 1 !important;
  visibility: visible !important;
  transform: rotateX(0deg);
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.1);
}
