* {
  /* width */
  &::-webkit-scrollbar {
    width: 8px;
    height: 6px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background-color: rgba(#b5bdcc, 0.329);
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: rgba($my-blue, 0.9);
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
    background: $my-blue;
  }
}
