.btn {
  &-more {
    height: 46px;
    font-size: 14px;
    font-family: "Montserrat-Medium", Arial, Helvetica, sans-serif;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 1.5;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;

    &__blue {
      border-radius: 23px;
      background-color: $my-blue;
      box-shadow: 0px 2px 5px 0px rgba(15, 46, 81, 0.41);
      color: rgb(255, 255, 255);
      padding: 12px 25px;
      min-width: 200px;

      &:hover {
        color: #333;
        background-color: $my-gray;
        color: rgb(41, 47, 59);
      }

      &-square {
        border-radius: 0;
        background-color: #1284d3;
        background-image: url("../img/blue-square.png");
        @extend %bgcover;
        min-width: 170px;
        font-size: 16px;
        color: rgb(255, 255, 255);
        text-transform: uppercase;
        font-weight: normal;
        &:hover {
          color: white;
          background-image: none;
        }

        &-2 {
          min-width: unset;
          max-width: 200px;
        }
      }
    }

    &__white {
      border-radius: 23px;
      background-color: $my-gray;
      color: rgb(41, 47, 59);
      padding: 6px 25px;
      min-width: 150px;

      &:hover {
        color: #fff;
        background-color: $my-blue;
      }
    }

    &__news {
      border-radius: 0;
      background-color: #1284d3;
      @extend %bgcover;
      width: 170px;
      font-size: 14px;
      color: rgb(255, 255, 255);
      text-transform: uppercase;
      font-weight: normal;
      padding: 5px 10px;
      &:hover {
        color: white;
        background-color: $my-blue;
        opacity: .9;
      }
    }
  }
}
