.background {
  padding: 20px;
  background-image: url("../img/background-item_bg.png");
  @extend %bgcover;

  background-color: rgb(255, 255, 255);
  box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.2);
  transition: all 0.3s ease-in-out;
  cursor: pointer;

  @include mq("desktop", min) {
    width: 250px;
    height: 240px;
  }

  &:hover {
    background-image: none;
  }

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;

  &__icon {
    @extend %bgcontain;
    width: 120px;
    height: 50px;
    &-1 {
      background-image: url("../img/background-icon1.png");
    }
    &-2 {
      background-image: url("../img/background-icon2.png");
    }
    &-3 {
      background-image: url("../img/background-icon3.png");
    }
    &-4 {
      background-image: url("../img/background-icon4.png");
    }

    margin-bottom: 15px;
    @include mq("tablet", max) {
      width: 80px;
      height: 35px;
    }
  }

  color: white;
  text-transform: uppercase;
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
  font-weight: normal;

  &__number {
    margin-bottom: 15px;
    font-size: 48px;
    line-height: 0.833;
    text-align: left;
    text-shadow: 0px 2px 0px rgba(15, 46, 81, 0.57);
    border-bottom: 1px solid #fff;
    padding-bottom: 10px;
    width: 100%;
    text-align: center;
    @include mq("tablet", max) {
      font-size: 32px;
    }
  }

  &__title {
    font-size: 24px;
    text-transform: uppercase;
    line-height: 1.667;
    text-align: left;
    text-shadow: 0px 2px 0px rgba(15, 46, 81, 0.57);
    @include mq("desktop", max) {
      font-size: 22px;
    }

    @include mq("tablet", max) {
      font-size: 16px;
    }
  }

  &:hover {
    &:hover {
      color: $my-blue;
      .background__number {
        border-bottom-color: $my-blue;
      }

      .background__icon {
        &-1 {
          background-image: url("../img/background-icon1-hover.png");
        }
        &-2 {
          background-image: url("../img/background-icon2-hover.png");
        }
        &-3 {
          background-image: url("../img/background-icon3-hover.png");
        }
        &-4 {
          background-image: url("../img/background-icon4-hover.png");
        }

        margin-bottom: 15px;
      }
    }
  }
}

.news-item {
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
  &__img {
    height: 300px;
    width: 100%;
    object-fit: cover;
  }

  &__date {
    font-size: 14px;
    line-height: 1.2;
    color: rgb(22, 81, 200);
    margin-right: 10px;
  }

  &__comments-count {
    font-size: 14px;
    line-height: 1.2;
    color: #999;
    margin-left: 20px;

    a {
      &:hover {
        text-decoration: underline;
      }
    }

    position: relative;
    &::before {
      position: absolute;
      content: "-";
      left: -20px;
    }
  }

  &__title {
    font-size: 18px;
    color: #333;
    font-weight: 600;
    margin-bottom: 20px;
  }

  &__subtitle {
    font-size: 14px;
    color: #999;
  }
  transition: 0.2s ease;
  &:hover {
    .news-item {
      &__title {
        text-decoration: underline;
        color: $my-blue;
      }
    }
  }
}

.media-item {
  img {
    @extend %tr03;
  }
  overflow: hidden;
  i {
    position: absolute;
    @extend %c-c;
    font-size: 60px;
    color: #f43535;
    @extend %tr03;
  }
  position: relative;
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: repeating-linear-gradient(
      180deg,
      rgba(34, 34, 34, 0) 0,
      rgba(34, 34, 34, 0) 30%,
      rgba(34, 34, 34, 0.85) 100%
    );
  }
  &__desc {
    position: absolute;
    bottom: 10px;
    left: 10px;
    z-index: 9;
  }
  &__big {
    .media-item__img {
      img {
        height: 385px;
        width: 100%;
        object-fit: cover;
        @include mq("tablet", max) {
          height: 250px;
        }
      }
    }
    .media-item__title {
      font-size: 18px;
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      color: rgb(255, 255, 255);
      line-height: 1.25;

      @include mq("tablet", max) {
        font-size: 14px;
      }
    }
    .media-item__date {
      font-size: 14px;
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      color: rgb(255, 255, 255);
      line-height: 1.25;

      @include mq("tablet", max) {
        font-size: 12px;
      }
    }

    &--only-image {
      i {
        display: none;
      }
    }
  }

  &__small {
    i {
      @include mq("tablet", min) {
        opacity: 0;
        visibility: hidden;
        font-size: 40px;
      }
    }
    .media-item__img {
      img {
        height: 180px;
        width: 100%;
        object-fit: cover;
        @include mq("tablet", max) {
          height: 250px;
        }
      }
    }
    .media-item__title {
      font-size: 14px;
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      color: rgb(255, 255, 255);
      line-height: 1.25;
    }
    .media-item__date {
      font-size: 12px;
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      color: rgb(255, 255, 255);
      line-height: 1.25;
    }

    &--only-image {
      i {
        display: none;
      }
    }
  }

  &:hover {
    .media-item {
      &__img {
        img {
          transform: scale(1.1);
        }
        i {
          @include mq("tablet", min) {
            opacity: 1;
            visibility: visible;
          }
        }
      }

      &__title {
        text-decoration: underline;
        // color: $my-blue;
      }
    }
  }
}

.partner {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0;
  width: 200px;
  @include mq("tablet", max) {
    width: 100%;
  }
  &__img {
    @extend %bgcontain;
    width: 100%;
    height: 150px;
  }

  background-color: rgb(255, 255, 255);
  box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.2);
  padding: 20px;

  &__name {
    font-size: 22px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: rgb(47, 47, 47);
    line-height: 1.083;

    @include mq("tablet", max) {
      font-size: 20px;
    }
  }

  &__profession {
    font-size: 16px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: rgb(1, 50, 140);
    line-height: 1.688;
    font-weight: normal;

    @include mq("tablet", max) {
      font-size: 14px;
    }
  }

  &__description {
    font-size: 16px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: rgb(86, 86, 86);
    line-height: 1.625;

    @include mq("tablet", max) {
      font-size: 14px;
    }
  }

  &__content {
    @include mq("tablet", max) {
      padding: 15px;
    }
    & + .consultant__footer {
      margin-top: 20px;
      width: 350px;
      min-height: 43px;

      @include mq("tablet", max) {
        width: 100%;
      }
    }
  }
}

.consultant {
  background-color: #fff;
  &__img {
    border-radius: 50%;
    height: 150px;
    width: 150px;
    background-color: $my-gray;
    overflow: hidden;

    img {
      height: 150px;
      width: 100%;
      object-fit: cover;
    }
  }

  &__name {
    font-size: 16px;
    font-family: "Montserrat-SemiBold", Arial, Helvetica, sans-serif;
    color: rgb(17, 17, 17);
    text-transform: uppercase;
    text-align: center;
    margin-top: 10px;
  }

  &__degree {
    font-size: 12px;
    font-family: "Montserrat-SemiBold", Arial, Helvetica, sans-serif;
    color: rgb(0, 70, 212);
    text-align: center;
    margin-top: 10px;
  }

  &__body {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 30px 15px;
  }

  &__footer {
    background-image: url("../img/consultant_bg.png");
    @extend %bgcover;
    min-height: 40px;
    width: 100%;
    display: flex;
    align-items: center;
  }

  &__phone {
    width: 50%;
    text-align: left;
    padding: 10px 10px;

    font-size: 14px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: rgb(255, 255, 255);
    line-height: 1;
    &:hover {
      color: white;
    }
  }

  &__socials {
    padding: 10px 10px;
    width: 50%;
    text-align: right;

    a {
      color: white;
      font-size: 14px;
      margin-left: 10px;
      display: inline-block;

      &:hover {
        transform: translateY(-5px);
      }
    }
  }
}

.news {
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
  &:not(:last-of-type) {
    margin-bottom: 3rem;
    @include mq("tablet", max) {
      margin-bottom: 1rem;
    }
  }
  &__img {
    a {
      width: 100%;
      height: 100%;
      display: block;
    }
    height: 450px;
    width: 100%;
    overflow: hidden;
    img {
      width: 100%;
      height: 450px;
      object-fit: cover;
      @include mq("tablet", max) {
        height: 300px;
      }
      transition: all 0.2s ease;
    }
    position: relative;

    @include mq("tablet", max) {
      height: 300px;
    }
  }

  &:hover {
    .news {
      &__img {
        // img {
        //   transform: scale(1.1);
        // }
      }

      &__title {
        text-decoration: underline;
        color: $my-blue;
      }
    }
  }

  &__content {
    margin-top: 20px;
    @include mq("tablet", max) {
      padding: 15px;
      margin-top: 0;
    }
  }

  &__widjets {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-size: 14px;

    & > div:not(:last-of-type) {
      margin-right: 20px;
      @include mq("tablet", max) {
        margin-right: 15px;
      }
      a {
        &:hover {
          span {
            text-decoration: underline;
          }
        }
      }
    }
    i {
      margin-right: 3px;
      color: $my-blue;
    }

    @include mq("tablet", max) {
      font-size: 12px;
    }
  }

  &__title {
    font-size: 24px;
    color: rgb(17, 17, 17);
    line-height: 1.281;
    font-weight: 600;
  }

  &__subtitle {
    font-size: 14px;
    color: rgb(68, 68, 68);
    @include text-clamp(3, 62px);
  }

  &-small {
    &:not(:last-of-type) {
      margin-bottom: 20px;

      @include mq("tablet", max) {
        margin-bottom: 0;
      }
    }
    .news {
      &__img {
        height: 250px;
        img {
          height: 250px;
          @include mq("tablet", max) {
            height: 300px;
          }
        }
        @include mq("tablet", max) {
          height: 300px;
          display: none;
        }
      }

      &__title {
        font-size: 16px;
      }

      &__widjets {
        font-size: 12px;
      }

      &__subtitle {
        font-size: 12px;
        height: 52px;
        @include mq("tablet", max) {
          display: none;
        }
      }

      &__content {
        margin-top: 10px;
        @include mq("tablet", max) {
          padding: 0 0 0px;
          border-bottom: 1px solid #99999957;
          margin: 0px 15px 10px;
        }
      }
    }
  }

  &-very-small {
    &:not(:last-of-type) {
      margin-bottom: 20px;

      @include mq("tablet", max) {
        margin-bottom: 0;
      }
    }
    .news {
      &__img {
        height: 60px;
        width: 100%;
        img {
          width: 100%;
          height: 60px;
          @include mq("tablet", max) {
            height: 100px;
          }
        }
        @include mq("tablet", max) {
          height: 100px;
          display: none;
        }
      }

      &__content {
        margin-top: 0;
        @include mq("tablet", max) {
          padding: 0;
          border-bottom: 1px solid #99999957;
          margin: 0px 15px 10px;
        }
      }

      &__widjets {
        & > div:not(:last-of-type) {
          margin-right: 10px;
        }
        font-size: 10px;
        @include mq("desktop", max) {
          font-size: 8px;
        }

        @include mq("tablet", max) {
          font-size: 12px;
        }
      }

      &__title {
        font-size: 12px;
        @include mq("tablet", max) {
          font-size: 16px;
        }
      }
    }
  }

  &-full {
    .news {
      &__subtitle {
        -webkit-line-clamp: unset;
        height: 100%;
        display: block;
        -webkit-box-orient: unset;
        overflow: visible;
      }
    }
  }
}

.in-socials {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  justify-content: space-between;
  @include mq("tablet", max) {
    padding-left: 15px;
    padding-right: 15px;
  }
  &__img {
    margin-bottom: 15px;
    width: 30%;
    img {
      width: 100%;
      height: 90px;
      object-fit: cover;
    }
  }
}

.rek {
  margin-bottom: 20px;

  @include mq("tablet", max) {
    margin-left: 15px;
    margin-right: 15px;
  }

  span {
    margin-top: 2px;
    display: block;
    text-align: center;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    font-size: 12px;
    color: #999;
    font-weight: 300;
  }
}

.feature-text.left-icon .feature-info {
  text-align: left;
  padding-left: 60px;
}

.feature-text .feature-icon span,
.feature-text .feature-icon i {
  font-size: 40px;
  margin-bottom: 30px;
  display: inline-block;
  padding-left: 15px;
  transition: 0.5s ease-in-out;
  -webkit-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  color: $my-blue;
}

.feature-text.left-icon .feature-icon {
  position: absolute;
  left: 0;
}

.feature-text {
  @include mq("tablet", max) {
    position: relative;
    padding: 15px;
  }
  h5 {
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 18px;
  }
  p {
    font-weight: normal;
    line-height: 1.5;
    color: #626262;
  }

  p {
    margin-top: 0;
    margin-bottom: 1rem;
  }
}

.list {
  li {
    font-size: 14px;
    i {
      color: $my-blue;
      margin-right: 5px;
    }
  }
}

.our-service {
  & > h6 {
    font-weight: 600;
    color: #000;
    border-bottom: 20px;
  }
  ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
    margin-bottom: 40px;

    li {
      margin-bottom: 15px;
      width: 100%;

      a {
        box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
        font-weight: 500;
        color: #333;
        display: block;
        font-size: 16px;
        background-color: #ffffff;
        padding: 12px 15px;
        display: block;

        &:hover, &.active {
          background-color: $my-gray;
        }
      }
    }
  }
}

.download-box {
  h6 {
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 18px;
  }

  .mb-15 {
    margin-bottom: 15px !important;
  }

  .iconbox {
    padding-left: 60px;
  }

  .mt-20 {
    margin-top: 20px !important;
  }

  .iconbox {
    position: relative;
  }

  .iconbox a {
    height: 50px;
    width: 50px;
    text-align: center;
    color: #ffffff;
    position: absolute;
    left: 0px;
    border-radius: 3px;

    &:hover {
      color: $my-blue;
    }
  }
  .theme-bg {
    background: $my-blue;

    &:hover {
      background-color: $my-gray;
    }
  }

  .iconbox i {
    font-size: 18px;
    line-height: 50px;
  }

  .font-weight-bold {
    font-weight: 700 !important;
  }

  .iconbox p {
    font-size: 11px;
  }
}
