.about-us {
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.2);
  @include mq("tablet", min) {
    padding: 25px 30px;
  }
  .col-lg-7 {
    @include mq("tablet", max) {
      padding: 15px 25px;
    }
  }

  text-align: left;
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
  h2 {
    font-size: 30px;
    color: rgb(0, 44, 66);
    line-height: 1.2;

    @include mq("tablet", max) {
      // text-align: center;
    }
  }

  p {
    font-size: 14px;
    color: rgb(96, 96, 96);
    line-height: 1.375;
    // text-align: center;
  }

  &__content {
    overflow-y: auto;
    max-height: 280px;
    padding-right: 5px;
  }

  .phone {
    margin-top: 20px;
    display: flex;
    // align-items: center;
    width: 100%;

    &-icon {
      width: 20%;
      height: 90px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-image: url("../img/phone-icon_bg.png");
      @extend %bgcover;

      @include mq("tablet", max) {
        width: 25%;

        img {
          max-width: 40px;
        }
      }
    }

    &-number {
      width: 80%;
      height: 90px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      background-image: url("../img/phone-number_bg.png");
      @extend %bgcover;
      @include mq("tablet", max) {
        width: 75%;
      }

      p {
        font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
        text-align: center;
        color: rgb(255, 255, 255);
        font-size: 18px;
        margin: 0;
        transform: translateY(5px);

        @include mq("tablet", max) {
          font-size: 16px;
        }
      }

      a {
        color: rgb(255, 255, 255);
        font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
        text-align: center;
        font-size: 40px;

        @include mq("tablet", max) {
          font-size: 28px;
        }
      }
    }
  }
}

.section-projects {
  margin-top: 75px;

  & > h1 {
    font-size: 48px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: rgb(22, 68, 130);
    font-weight: normal;
    line-height: 0.5;
    text-align: center;
  }

  .projects {
    @extend %bgcover;
    min-height: 500px;
    position: relative;
    .container {
      @include mq("tablet", min) {
        position: absolute;
        z-index: 99;
        left: 0;
        right: 0;
      }
      height: 100%;
      width: 100%;

      .row {
        height: 100%;
      }
    }
  }
}

.section-background {
  margin-top: 30px;
}

.section-news_main {
  margin-top: 30px;

  .news_main {
    background-color: #fff;
    padding: 25px;
    @include mq("tablet", max) {
      padding: 25px 15px;
    }
    box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.2);

    &__type {
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      font-size: 16px;
      color: rgb(127, 130, 132);
      text-transform: uppercase;
    }

    &__title {
      font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
      font-size: 32px;
      font-weight: 500;
    }
  }
}

.section-multimedia {
  margin-top: 60px;
  padding: 50px 0;
  background-image: url("../img/multimedia_bg.png");
  @extend %bgcover;
  min-height: 600px;
  width: 100%;

  .section-title {
    font-size: 36px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: rgb(255, 255, 255);
    line-height: 1;
    text-align: left;
    border-bottom: 3px solid #fff;
    padding-bottom: 10px;
    display: inline-block;
  }
}

.section-partners {
  margin-top: 40px;

  .section-title {
    font-size: 60px;
    font-family: "RopaSansSCPTT-Regular", Arial, Helvetica, sans-serif;
    color: rgb(0, 44, 66);
    line-height: 1.2;
    text-align: center;
    font-variant: small-caps;
  }
}

.section-consultant {
  margin-top: 60px;
  padding: 50px 0;
  background-image: url("../img/multimedia_bg.png");
  @extend %bgcover;
  min-height: 450px;
  width: 100%;
  .link-all {
    font-size: 16px;
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: rgb(255, 255, 255);
    text-decoration: underline;
    text-transform: uppercase;
  }
  .section-title {
    font-size: 36px;
    @include mq("phablet", max) {
      font-size: 28px;
    }
    font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
    color: rgb(255, 255, 255);
    line-height: 1;
    text-align: left;
    display: inline-block;
    text-transform: uppercase;
  }
}

.section-all_partners {
  padding: 50px 0;
  .all_partners {
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.2);
    @include mq("tablet", min) {
      padding: 25px 30px;
    }
  }
}

.sectiom-all_news {
  padding: 50px 0;
  .all_news {
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.2);
    @include mq("tablet", min) {
      padding: 25px 30px;
    }

    .pagination {
      @include mq("tablet", max) {
        padding: 0 15px;
      }
    }
  }

  .news-left_side {
    &--title {
      font-family: "Montserrat-SemiBold", Arial, Helvetica, sans-serif;
      font-size: 18px;
      text-transform: uppercase;
      margin-bottom: 15px;

      @include mq("tablet", max) {
        padding-left: 15px;
      }
    }
  }

  .archive {
    @include mq("tablet", max) {
      margin-left: 15px;
      margin-right: 15px;
    }
    padding: 0;
    list-style-type: none;
    li {
      margin: 10px 0;
      font-size: 14px;
      i {
        color: #444;
      }
      a {
        font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
        color: #444;
        font-weight: normal;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}

.section-about-us {
  padding: 60px 0;
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;

  .abouts {
    @include mq("tablet", max) {
      padding: 15px;
    }

    h6 {
      font-size: 16px;
      font-weight: 500;
      margin-bottom: 10px;
    }

    h3 {
      font-size: 26px;
      font-weight: 700;
      margin-bottom: 20px;
    }

    h5 {
      font-size: 15px;
      margin-bottom: 30px;
    }

    p {
      font-size: 12px;
    }
  }

  & + .section-services {
    .swiper-service {
      margin-top: 0 !important;
    }
  }
}

.section-faq {
  padding: 0 0 60px;
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
  .faq {
    @include mq("tablet", min) {
      padding: 25px 30px;
    }
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.2);
    @include mq("tablet", max) {
      padding: 15px;
    }
  }
}

.section-all_service {
  padding: 60px 0;
  font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
  .all_service {
    @include mq("tablet", min) {
      padding: 25px 30px;
    }
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 16px 35px 0px rgba(15, 46, 81, 0.2);
    @include mq("tablet", max) {
      padding: 15px;
      padding-top: 0;
    }

    .feature-text.left-icon .feature-info {
      @include mq("tablet", max) {
        padding-left: 50px;
      }
    }

    .feature-text .feature-icon i, .feature-text .feature-icon span {
      @include mq("tablet", max) {
        padding-left: 0;
      }
    }
  }
  .section-title {
    h2 {
      font-weight: 600;
      font-size: 28px;
    }

    p {
      font-size: 14px;
      font-weight: normal;
    }
  }

  h4 {
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 20px;

    &~p {
      font-size: 14px;
    }
  }

  .feature-info {
    h5 {
      font-size: 16px;
    }

    p {
      font-size: 14px;
    }

  }

  .my-col-12 {
    padding-left: 0;
    padding-right: 0;

  }
}
