header {
  // background-image: url('../../img/header_bg1.png');
  // background-attachment: fixed;
  @extend %bgcover;
  height: 400px;
  position: relative;
  overflow: hidden;
  .parallax {
    min-width: 100%;
    height: 400px;
  }

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 12;
    background-image: repeating-linear-gradient(
      180deg,
      #0e5fd4,
      #0e5fd455 25%,
      #0e60d455 100%
    );
  }

  .header-container {
    position: absolute;
    bottom: 50px;
    z-index: 22;
    width: 100%;
    left: 0;
    right: 0;

    .page-title {
      color: white;
      h2 {
        font-family: "Montserrat-Medium", Arial, Helvetica, sans-serif;
        font-size: 32px;
        text-transform: uppercase;
      }

      p {
        font-family: "Montserrat-Regular", Arial, Helvetica, sans-serif;
        font-weight: normal;
        font-size: 14px;
      }
    }
  }
}
