#myBtn {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 15px;
  z-index: 99;
  font-size: 18px;
  border: none;
  outline: none;
  background-color: $my-blue;
  color: white;
  cursor: pointer;
  padding: 15px;
  border-radius: 4px;

  width: 50px;
  height: 50px;
  border-radius: 50%;

  justify-content: center;
  align-items: center;
}

#myBtn:hover {
  background-color: #555;
}
